import React, { Component } from 'react';

class Selector extends Component {
  constructor( { cb1_1 }){
    super();
    this.state = cb1_1;
    this.select = this.select.bind(this);
  }
  select(e) {
    this.setState({ select : e.target.value});
    this.props.callbackParent1_1(e.target.value);
  }

  render() {
    return (
      <div className="input-group col-6">
        <select className="form-control" ref="cur1" onChange={this.select} >
          <option ></option>
          <option value="SKA/BTC">Sikka to Bitcoin</option>
          <option value="BTC/SKA">Bitcoin to Sikka</option>
          <option value="SKA/ETH">Sikka to Etheruem</option>
          <option value="ETH/SKA">Etheruem to Sikka</option>
          <option value="SKA/MCAP">Sikka to MCAP</option>
          <option value="MCAP/SKA">MCAP to Sikka</option>
          <option value="SKA/BCH">Sikka to Bitcoin Cash</option>
          <option value="BCH/SKA">Bitcoin Cash to Sikka</option>
        </select>
      </div>
    )
  }
}
export default Selector;



