import React, { Component } from 'react';
import Selector from './selector'

class stage1 extends Component {
  constructor( { cb1 }) {
    super();
    this.submit = this.submit.bind(this);
    this.state = { Exchange : "Cur1/cur2" };
  }
  selectChange(newState) {
    this.setState({ Exchange: newState  })
  }
  submit() {
    this.setState({  stage2 : true, stage1 : false })
    setTimeout(()=>{
      this.props.callbackParent1(this.state);
    },200)

  }

  render() {
    return (
      <div className="form-group">
        <h1>Sikka Converter</h1>
        <div>
          <p className="text-muted">Select currency transfers </p>
          <Selector cb1_1={this.state} callbackParent1_1={(newState) => this.selectChange(newState) } />
        </div>
        <p className="text-muted"></p>
        <div className="col-6">
          <button type="button" className="btn btn-primary " onClick={this.submit}> Proceed</button>
        </div>
      </div>
    )
  }
}
export default stage1;



