import React, { Component } from 'react';
import './App.css';
import Stage1 from './views/Sikka/stage1.js'
import Stage2 from './views/Sikka/stage2.js'
import Stage3 from './views/Sikka/stage3.js'
import ToggleDisplay from 'react-toggle-display';

class Main extends Component {
  constructor(props) {
    super(props);
    this.init = this.init.bind(this);
    this.fetchRate = this.fetchRate.bind(this);
    this.onStageChange = this.onStageChange.bind(this);
    this.state = { stage1:true, stage2:false, stage3 :false };
    this.fetchRate();
    this.init();
  }

  init(){
    var self = this;
    if (window.location.pathname.split('/')[1] !== ""){
      var id = window.location.pathname.split('/')[1];
      // fetch
      fetch('./api/v1/fetchId',{method: "GET"}).then( function(res) {
        res.json().then((d)=>{
          self.setState({
            stage1    : false,
            stage3    : true,
            Txid      : id,
            TxAddr    : d.TxAddr,
            TxState   : d.TxState,
            InHash    : d.InHash,
            OutHash   : d.OutHash,
            Rate      : d.Rate,
            Exchange  : d.Exchange,
            C1Addr    : d.C1Addr,
            C2Addr    : d.C2Addr,
            C1Value   : d.C1Value,
            C2Value   : d.C2Value,
            StartDate : d.StartDate,
            EndDate   : d.EndDate
          })
        })
      });
    }
  }
  onStageChange(newState) {
    this.setState(newState);
  }
  fetchRate() {
    var self = this;
    var rates;
    fetch('./api/v1/getRates',{method: "GET"}).then( function(res) {
      res.json().then((data)=>{
        rates = data;
      })
    });
    fetch('./api/v1/getBalance',{method: "GET"}).then( function(res) {
      res.json().then((data)=>{
        self.setState({ Rates : rates, Balance : data })
        console.log(self.state);
      })
    })
    setInterval( ()=> {
      fetch('./api/v1/getRates',{method: "GET"}).then( function(res) {
        res.json().then((data)=>{
          rates = data;
        })
      });
      fetch('./api/v1/getBalance',{method: "GET"}).then( function(res) {
        res.json().then((data)=>{
          self.setState({ Rates : rates, Balance : data })
          console.log(self.state);
        })
      })
    },10000)
  }

  render() {
    return (
      <div className="form-group">
        <ToggleDisplay if={this.state.stage1} >
          <Stage1 cb1={this.state} callbackParent1={(newState) => this.onStageChange(newState) }/>
        </ToggleDisplay>
        <ToggleDisplay if={this.state.stage2} >
          <Stage2 cb2={this.state} callbackParent2={(newState) => this.onStageChange(newState) }/>
        </ToggleDisplay>
        <ToggleDisplay if={this.state.stage3} >
          <Stage3 cb3={this.state} callbackParent3={(newState) => this.onStageChange(newState) }/>
        </ToggleDisplay>
      </div>
   );
  }
}

/*
  <ToggleDisplay if={this.state.stage2} >
    <Stage2 cb2={this.state} callbackParent2={(newState) => this.onStageChange(newState) }/>
  </ToggleDisplay>
  <ToggleDisplay if={this.state.stage3} >
    <Stage3 cb3={this.state} callbackParent3={(newState) => this.onStageChange(newState) }/>
  </ToggleDisplay>
*/

export default Main;



